
/* Top bar */
.top-bar { 
  background-color: $color-background-accent;
  background-color: var(--color-background-accent);
  border-bottom: solid 1px rgba(0,0,0,.25);
  display: grid;
  grid-template-columns: max-content 1fr;
  padding: 0.5rem 2vw 0;
  *:last-child {
    justify-self: right;
  }
}


/* Navigation */

.title-link {
  font-weight: 600;
  @supports (font-variation-settings: normal) {
    font-weight: 625;
    .wf-inactive & {
      font-weight: bold;
    }
  }
  &:link,
  &:visited {
    text-decoration: none;
  }
  &:hover {
    text-decoration: underline;
  }
  &:focus {
    text-decoration: underline;
  }
  &:active {
    text-decoration: underline;
  }
  @media (pointer: coarse) {
    &:hover {
      text-decoration: unset;
    }
  }
}
.link-wrapper {
  display: flex;
}

.settings {
  --text-opsz: 10;
  display: inline-block;
  font-family: $fonts-serif-static-caption;
  font-family: var(--fonts-serif-static-caption);
  position: relative;
  @supports (font-variation-settings: normal) {
    font-family: var(--fonts-serif);
    font-variation-settings: 'opsz' var(--text-opsz);
    .wf-inactive & {
      font-family: var(--fonts-base-serif);
      font-size: 1.1em;
    }
  }

  &-trigger {
    display: block;
    height: 2rem;
    min-width: 7rem;
    text-align: right;
    &:link,
    &:visited {
      text-decoration: none;
    }
    &:hover {
      text-decoration: underline;
    }
    &:focus {
      text-decoration: underline;
    }
    &:active {
      text-decoration: underline;
    }
    @media (pointer: coarse) {
      &:hover {
        text-decoration: unset;
      }
    }
  }

  &-menu {

    .settings & {
      font-size: 0.92rem;
      height: 1px;
      list-style: none;
      margin: 0;
      opacity: 0;
      overflow: hidden;
      padding: 0.25rem;
      position: absolute;
      top: 2rem;
      transition: opacity 0.2s ease-in, height 0.1s ease-in;
      left: 0;
      width: 100%;
      li {
        border-bottom: solid 1px rgba(0,0,0,.25);
        &:last-child {
          border-bottom: none;
        }
        &.active {
          padding: 0.5em 0.15em;
        }
      }
  
      a {
        display: block;
        font-size: 0.875rem;
        padding: 0.5em 0.15em;
        .wf-inactive & {
          font-size: 0.9rem;
          padding: 0.475em 0.15em;
        }
  
        &:link {
          text-decoration: none;
        }
        
        &:visited {
          text-decoration: none;
        }
        
        &:hover {
          background-color: rgba(0,0,0,.05);
          text-decoration: none;
        }
        
        &:focus {
          background-color: rgba(0,0,0,.05);
          text-decoration: none;
        }
        
        &:active {
          background-color: rgba(0,0,0,.05);
          text-decoration: none;
        }
      }
    }
    .settings:focus-within &,
    .settings:hover & {
        background-color: $color-background-accent;
        background-color: var(--color-background-accent);
        border-left: solid 1px rgba(0,0,0,.25);
        box-shadow: 0.1em 0.1em 0.2em #eaeaea;
        min-height: 8rem;
        opacity: 1;
        overflow-y: scroll;
        z-index: 99;
    }
  }
}

