/* Settings */
.toggle-swipe {
  &.on:after {
    content: ": on";
  }
  &.off:after {
    content: ": off";
  }
}



/* Chapter prev/next navigation */
.prev-next-chapters {
  display: flex;
  justify-content: space-between;
  flex-direction: column-reverse;
  list-style: none;
  margin: 0 auto;
  margin: calc(var(--body-text-spacer) * 2) 0 0 0;
  max-width: var(--content-width);
  padding: 0;

  @media screen and (min-width: $bp--small) {
    flex-direction: row;
  }

  > * {
    display: block;
    flex-grow: 1;
    margin-bottom: 1rem;
    margin-bottom: var(--body-text-spacer);
  }

  .previous {
    text-align: left;
  }

  .next {
    text-align: right;
  }

  a {
    &:link,
    &:visited {
      --text-opsz: 10;
      font-family: $fonts-serif-static-caption;
      font-family: var(--fonts-serif-static-caption);
      font-size: 0.875rem;
      text-decoration: none;
      @supports (font-variation-settings: normal) {
        font-family: var(--fonts-serif);
        font-variation-settings: 'opsz' var(--text-opsz);
        .wf-inactive & {
          font-family: var(--fonts-base-serif);
        }
      }
      @media screen and (min-width: $bp--large) {
        font-size: 1rem;
      }
    }
    &:hover,
    &:focus,
    &:active {
      text-decoration: underline;
    }
    @media (pointer: coarse) {
      &:hover {
        text-decoration: unset;
      }
    }
  }
}

/* Book title */
.publication-title {
  font-weight: 900;
}

/* Editor's note */
.note {
  background-color: #ffffff;
  border: solid 1px #eaeaea;
  max-width: var(--content-width);
  padding: 0.5rem;
  figure + & {
    margin-top: 1rem;
    margin-top: var(--body-text-spacer);
  }
  > *:first-child {
    margin-top: 0;
  }
}

figure {
  //margin: 0;
  padding: 0;
  max-width: $content-width-s;
  max-width: var(--content-width);
  width: 100%;
  @media screen and (min-width: $bp--medium) {
    max-width: $content-width-m;
    max-width: var(--content-width);
  }
  @media screen and (min-width: $bp--large) {
    max-width: $content-width-l;
    max-width: var(--content-width);
  }

  img {
    margin: 0;
    max-height: 100%;
    max-width: 100%;
    object-fit: contain;
    padding: 0;
    width: auto;
  }

  &.wide {
    margin-left: calc( var(--margin-left) / 2 );
    margin-right: calc( var(--margin-left) / 2 );
    max-width: calc( 100vw - var(--margin-left) );
    width: 100%;
  }

  &.small {
    --figure-width: calc( var(--content-width) / 3);
    --centering-margin: calc( var(--content-width) / 3);
    width: var(--figure-width);
  }

  &.medium {
    --figure-width: calc( var(--content-width) / 2);
    --centering-margin: calc( var(--content-width) / 4);
    width: var(--figure-width);
  }

  &.large {
    max-width: var(--content-width);
    width: 100%;
  }

  &.center {
    margin-left: calc( var(--margin-left) + var(--centering-margin) );
  }

  &.left {
    clear: left;
    float: left;
    margin-right: 1.5rem;
    margin-bottom: 1rem;
    margin-bottom: var(--body-text-spacer);
  }

  &.right {
    clear: right;
    float: right;
    margin-left: 1.5rem;
    margin-right: var(--margin-right);
    margin-bottom: 1rem;
    margin-bottom: var(--body-text-spacer);
  }

  * + & {
    margin-top: 1rem;
    margin-top: var(--body-text-spacer);
  }
}

/* Blockquote styles */
blockquote {
  margin-bottom: var(--body-text-spacer);
  * + & {
    margin-top: var(--body-text-spacer);
  }
  p {
    font-size: $bq-size-s;
    font-size: var(--bq-size-s);
    font-variant-ligatures: common-ligatures discretionary-ligatures;
    .wf-inactive & {
      letter-spacing: 0;
      word-spacing: 0.05em;
      em {
        letter-spacing: -0.0025em;
        word-spacing: -0.01em;
      }
    }
      @media screen and (min-width: $bp--medium) {
      font-size: $bq-size-m;
      font-size: var(--bq-size-m);
    }
    @media screen and (min-width: $bp--large) {
      font-size: $bq-size-l;
      font-size: var(--bq-size-l);
    }
    @supports (font-size: clamp(1rem, 2vw, 3rem)) {
      /* responsive typography scaling bits */
      --font-size-min-value: var(--bq-size-s-value);
      --font-size-max-value: var(--bq-size-l-value);
      --font-size-scaler-value: var(--bq-scaler-value);
      font-size: var(--font-size);
  
      --font-lh-min-value: var(--bq-lh-s);
      --font-lh-max-value: var(--bq-lh-l);
      --font-lh-scaler-value: var(--bq-lh-scaler-value);
      line-height: var(--line-height); 
    }
    &:before {
      content: 'โ';
      margin-left: -0.5em;
    }
    &:after {
      content: 'โ';
    }

  }
  &.bq--inline {
    p {
      display: inline;
      font-size: $p-size-s;
      font-size: var(--p-size-s);
      font-variant-ligatures: common-ligatures;
      @media screen and (min-width: $bp--medium) {
        font-size: $p-size-m;
        font-size: var(--p-size-m);
      }
      @media screen and (min-width: $bp--large) {
        font-size: $p-size-l;
        font-size: var(--p-size-l);
      }
      @supports (font-size: clamp(1rem, 2vw, 3rem)) {
        /* responsive typography scaling bits */
        --font-size-min-value: var(--p-size-s-value);
        --font-size-max-value: var(--p-size-l-value);
        --font-size-scaler-value: var(--p-scaler-value);
        font-size: var(--font-size);
    
        --font-lh-min-value: var(--p-lh-s);
        --font-lh-max-value: var(--p-lh-l);
        --font-lh-scaler-value: var(--p-lh-scaler-value);
        line-height: var(--line-height); 
      }
    }

  }
  &.bq--plaque {
    padding-left: 5vw;
    padding-right: 5vw;
    p {
      border: solid 1px $color-dark-gray;
      border-color: var(--color-text);
      box-shadow: 1px 1px 2px rgba(0,0,0,.75), 2px 2px 2px rgba(0,0,0,.75);
      font-size: 0.9em;
      text-transform: lowercase;
      font-variant-caps: small-caps;
      -webkit-hyphens: none;
      hyphens: none;
      line-height: 1.75;
      margin-left: auto;
      margin-right: auto;
      max-width: 27em;
      padding: 1.5rem 1rem;
      text-align: center;
      &:before,
      &:after {
        content: "";
      }
    }
    .name {
      display: block;
      font-size: 1.25em;
      font-weight: 450;
    }
  }
  cite {
    font-size: $p-size-s;
    font-size: var(--p-size-s);
    font-style: italic;
    @media screen and (min-width: $bp--medium) {
      font-size: $p-size-m;
      font-size: var(--p-size-m);
    }
    @media screen and (min-width: $bp--large) {
      font-size: $p-size-l;
      font-size: var(--p-size-l);
    }
    @supports (font-size: clamp(1rem, 2vw, 3rem)) {
      /* responsive typography scaling bits */
      --font-size-min-value: var(--p-size-s-value);
      --font-size-max-value: var(--p-size-l-value);
      --font-size-scaler-value: var(--p-scaler-value);
      font-size: var(--font-size);
  
      --font-lh-min-value: var(--p-lh-s);
      --font-lh-max-value: var(--p-lh-l);
      --font-lh-scaler-value: var(--p-lh-scaler-value);
      line-height: var(--line-height); 
    }
    &:before {
      content: "โ";
    }
  }
}

.lang-table {
  list-style: none;
  padding: 0;
  .chars {
    display: inline-block;
    width: 13.5rem;
  }
  .language {
    display: inline-block;
    font-style: italic;
  }
}

article {
  > h1 {
    @media screen and (min-width: $bp--medium) {
      margin-left: calc( var(--margin-left) / 2 );
      margin-right: calc( var(--margin-left) / 2 );
      max-width: calc( 100vw - var(--margin-left) );
    }
  }
  figure + h1 {
    margin-top: 0.25rem;
    margin-top: calc( var(--body-text-spacer) * .25 );
  }
}

/* Footer */
body > footer {
  border-top: solid 1px rgba(0,0,0,0.15);
  margin-top: 3rem;
  padding: 0.5rem 2vw 1rem 2vw;
  text-align: center;
  @media screen and (min-width: $bp--small) {
    padding-left: var(--margin-left);
    text-align: left;
  }

  a {
    &:link,
    &:visited { 
      --text-opsz: 7;
      font-family: $fonts-serif-static-caption;
      font-family: var(--fonts-serif-static-caption);
      font-size: 0.625rem;
      text-decoration: none;
      @media screen and (min-width: $bp--small) {
        font-size: 0.75rem;
      }
      @supports (font-variation-settings: normal) {
        font-family: var(--fonts-serif);
        font-variation-settings: 'opsz' var(--text-opsz);
        .wf-inactive & {
          font-family: var(--fonts-base-serif);
          font-weight: normal;
          font-size: 0.8675rem;
        }
      }
    }
    &:hover,
    &:focus,
    &:active {
      text-decoration: underline;
    }
  }
}

.footnote {
  &--reference {
    &:link,
    &:visited {
      font-size: inherit;
      font-variant-numeric: lining-nums;
      font-feature-settings: 'numr' 1;
      font-weight: 500;
      line-height: inherit;
      vertical-align: inherit;
      padding-left: 0.1em;
      padding-right: 0.1em;
      text-underline-offset: -0.15em;
      scroll-padding: 2em 0 0 0;
    }
  }
}
.footnotes {
  padding-left: 1em;

  li {
    --p-size-s-value: 0.75;
    --p-size-m-value: 0.875;
    --p-size-l-value: 1;
    text-align: top;
    scroll-padding: 2em 0 0 0;
    + li {
      margin-top: calc( var(--body-text-spacer) * 1 );
    }
  }
}